import React, { Component } from 'react'
import { connect } from 'dva'
import { Select, Form } from 'antd'
const Option = Select.Option;
const SelectStyle = { width: 200, marginRight: 10 }
// 
function SelectChoose(props) {
  let {selected,selKey,changeSelected,text,sources,id,name} = props;
  return <React.Fragment>
    {text}<Select placeholder="aa" style={SelectStyle} value={selected} onChange={value => changeSelected({ [selKey]: value })}>
      {
        sources && sources.map((item, key) => {
          return <Option value={item[id]} key={key}>{item[name]}</Option>
        })
      }
    </Select>
  </React.Fragment>
}

let mapState = store => {
  return { ...store.addQuestions }
}
let mapDispatch = dispatch => {
  return {
    requestData() {
      dispatch({ type: 'addQuestions/requestData' })
    },
    changeSelected(payload) {
      dispatch({ type: 'addQuestions/initData', ...payload })
    }
  }
}
@connect(mapState, mapDispatch)
class Classify extends Component {
  render() {
    let { 
      examType, 
      questionsType, 
      subject, 
      exam_id, 
      subject_id, 
      questions_type_id, 
      changeSelected 
    } = this.props;
    return <div>
      <Form.Item label="">
      <SelectChoose
        selected={exam_id}
        selKey="exam_id"
        changeSelected={changeSelected}
        sources={examType}
        id="exam_id"
        name="exam_name"
      />
      </Form.Item>
      <Form.Item label="">
      <SelectChoose
        selected={subject_id}
        selKey="subject_id"
        changeSelected={changeSelected}
        sources={subject}
        id="subject_id"
        name="subject_text"
      />
      </Form.Item>
      <Form.Item label="">
      <SelectChoose
        selected={questions_type_id}
        selKey="questions_type_id"
        changeSelected={changeSelected}
        sources={questionsType}
        id="questions_type_id"
        name="questions_type_text"
      />
      </Form.Item>
    </div>
  }
  componentDidMount() {
    this.props.requestData();
  }
}
export default Classify;
