import React, { Component } from 'react'
import Editor from 'for-editor'
import {message} from 'antd'
class MdEditor extends Component {
  state = {
      editMd:''
  }
  static defaultProps = {
    editorChange(){}, // function
    editMd:'' //   string
  }
  static getDerivedStateFromProps = (newProps,nowState)=>{
    return {
      ...nowState,
      ...newProps
    }
  }
  render(){
    let {editMd} = this.state;
    return <Editor lineNum={true} value={editMd} onChange={this.handleChange} onSave={this.onSave} />
  }
  handleChange = (editMd)=>{
    this.setState({editMd});
    this.props.editorChange(editMd);
  }
  onSave = ()=>{
    let {editorId} = this.props;
    localStorage.setItem(editorId,this.state.editMd);
    message.success('',1);
  }
  componentDidMount(){
    let editMd = localStorage.getItem(this.props.editorId);
    if(editMd){
      this.setState({editMd});
      this.props.editorChange(editMd);
    }
  }
}
export default MdEditor;
