import React , {Component} from 'react'
import {connect} from 'dva'
import EditMdQues from '../../components/editQuestions'
import BaseContent from '@/components/BaseContent'
import {Modal} from 'antd'
import UtilGlobal from '../../utils/global'
import {getUrlParams} from '@/utils/utils'
let {basePath} = UtilGlobal;
let {confirm} = Modal;
let mapState = store => {
    return {...store.addQuestions,...store.watchQuestions,...store.main}
}
let mapDispatch = dispatch => {
    return {
        initData(payload){
            dispatch({type:'addQuestions/initData',...payload});
        },
        dispatch
    }
}
@connect(mapState,mapDispatch)
class EditQuestinons extends Component{
    state = {}
    static getDerivedStateFromProps = (nextProps)=>{
        if(nextProps.updatedStatus === 1){
            Modal.success({
              content:'',
              onOk:()=>{
                  nextProps.initData({updatedStatus:0});
                  nextProps.history.push({
                      pathname:`${basePath}/main/watchQuestions`,
                      state:{
                          parent:'',
                          child:'',
                          id:'main-watchQuestions'
                      }
                  });
              }
            });
          }else if(nextProps.updatedStatus === 3){
            Modal.error({
              content:'',
              onOk:()=>nextProps.initData({updatedStatus:0})
            });
          }
        return {...nextProps}
    }
    updatedQuestions = ()=>{
        confirm({
            title:'',
            content:'',
            onOk:()=>{
                let {questions_id} = this.props;
                let {
                    title,
                    questions_stem,
                    questions_answer,
                    subject_id,
                    questions_type_id,
                    exam_id
                } = this.props;
                this.props.dispatch({
                    type:'addQuestions/updateQUestions',
                    title,
                    questions_stem,
                    questions_answer,
                    subject_id,
                    questions_type_id,
                    exam_id,
                    questions_id
                })
            },
            onCancel:()=>{}
        })
       
    }
    componentDidMount () {
        const id = getUrlParams(window.location.href).id
        console.log('aaa')
        this.props.dispatch({type: 'addQuestions/getQuestions', id})
    }
    render(){
        let {
            title,
            questions_stem,
            questions_answer
        } = this.props
        return <React.Fragment>
            <BaseContent>
            <EditMdQues
               title={title}
               stemMd={questions_stem}
               answerMd={questions_answer}
               getTitleVal={(title)=>this.props.dispatch({type:'addQuestions/initData',title})}
               getQuestionsStem={(questions_stem)=>this.props.dispatch({type:'addQuestions/initData',questions_stem})}
               getQuestionsAnswer={(questions_answer)=>this.props.dispatch({type:'addQuestions/initData',questions_answer})}
               submitClick={()=>this.updatedQuestions()}
            />
            </BaseContent>
        </React.Fragment>
    }
}
export default EditQuestinons;